const _ToolStyleUtils = class {
};
let ToolStyleUtils = _ToolStyleUtils;
ToolStyleUtils.replaceAFromRGBA = (rgba, opacity) => {
  return rgba.split(" ").join("").replace(/,[0-9]+([.]{1}[0-9]+){0,1}\)/, `,${opacity.toFixed(2)})`);
};
ToolStyleUtils.getToolStrokeAndFill = (color, options) => {
  const {
    hover = false,
    selected = false,
    borderOpacity = 1,
    fillOpacity = 1,
    commonOpacity = 0.8
  } = options;
  if (selected || hover) {
    return {
      stroke: _ToolStyleUtils.replaceAFromRGBA(color, borderOpacity),
      fill: _ToolStyleUtils.replaceAFromRGBA(color, fillOpacity)
    };
  }
  return {
    stroke: _ToolStyleUtils.replaceAFromRGBA(color, borderOpacity * commonOpacity),
    fill: _ToolStyleUtils.replaceAFromRGBA(color, fillOpacity * commonOpacity)
  };
};
ToolStyleUtils.getAttributeIndex = (attribute, attributeList) => {
  try {
    const attributeIndex = attributeList.findIndex((i) => i.value === attribute);
    return attributeIndex;
  } catch (error) {
    return -1;
  }
};

export { ToolStyleUtils as default };
